.short-post {
    &__title {
        font-size: 16px;
        &:hover {
            text-decoration: underline;
        }
    }
    &__author:hover {
        text-decoration: underline;
    }
    &__date, &__author {
        font-size: 14px;
        color: #a1a1a1;
        line-height: 2;
    }
}
